  .icon:hover .one {
    margin: .5rem 0 0 .85rem; }
  .icon:hover .two {
    margin: 0 0 0 3.5rem; }
  .icon:hover .three {
    font-size: .8rem;
    margin: .1rem 0 0 4rem; }
  .icon div {
    font-style: normal;
    position: absolute;
    transition: transform 0.26s ease-in-out, margin 0.26s ease-in-out; }
  .icon .one {
    font-size: .8rem;
    margin: 0 0 0 2rem; }
  .icon .two {
