nav {
  font-family: 'Montserrat',sans-serif;
  height: 100vh;
  left: 0;
  top: 0;
  overflow: hidden;
  position: fixed;
  transition: width 0.26s ease-in-out;
  width: 15rem;
  font-size: 1rem;
  background-color: #f5f5f5; }
  nav.small {
    width: 2.8rem; }
    nav.small > .menu-item .sub-menu-indicator {
      opacity: 0; }
    nav.small ~ .content {
      padding-left: 2.8rem;
      width: calc(100% - 14.8rem); }
    nav.small > .menu-item .sub-menu {
      margin-left: -16rem; }
    nav.small > .menu-item .menu-item-wrapper span,
    nav.small > .settings span {
