  nav.hidden {
    width: 0; }
  nav .menu-item {
    cursor: pointer;
    display: flex;
    font-family: 'Montserrat',sans-serif;
    overflow: hidden;
    padding: 0 .2em;
    margin: .3rem;
    width: 14rem;
