      nav .menu-item .sub-menu-indicator svg[data-icon='angle-up'] {
        right: -.15em;
        opacity: 0; }
    nav .menu-item .sub-menu {
      margin: 2.1rem 0 0 -14.3rem;
      max-height: 0;
      overflow: hidden;
      transition: margin 0.26s ease-in-out; }
      nav .menu-item .sub-menu .menu-item {
        font-size: .8em;
        width: 11.7rem; }

.border {
